.dialogs {
display: grid;
grid-template-columns: 2fr 10fr;
    grid-template-areas:
   "dialog message"

}

.dialogsItem {
grid-area: dialog;
padding: 10px;
}

.dialog {
display: flex;
padding: 5px;
}

.dialogAvatar {
    width: 40px;
    height: 40px;
    border-radius: 50%;
    margin-right: 10px;

}

.messages {
    padding: 10px;
    grid-area: message;
}

.message {
   border: 1px black solid;
    border-radius: 40%;
    width: fit-content;
    padding: 10px;
    margin: 10px;
}

.error{
    color: red;
}